import { getSuccessRate } from '../../../utils'
import { MainTitle } from '../../generic'
import { Text } from '@chakra-ui/react'

export function Results({correctAnswers, wrongAnswers}) {
    const rate = getSuccessRate(correctAnswers, wrongAnswers)
    
    return (
      <>
          <MainTitle></MainTitle>
          <Text> :</Text>
          <Text
              fontWeight='bold'
              fontSize='2xl'>
                  {rate}%
          </Text>
      </>
    )
}
